import { x2 } from 'sha256'

const cred = {
  username: '718a3c3a76f5f9b5ca2b06358629ad9f2eba47e276202a1cd9896560a8ad1f32',
  password: 'c33a7594d49acff0e6172a9ce6554d2fb27da912248c08b8a9ba81ab692718b7'
}

export default function() {
  if (
    window.localStorage.getItem('username') === null ||
    window.localStorage.getItem('password') === null
  ) {
    return false
  }
  if (
    x2(window.localStorage.getItem('username')) === cred.username &&
    x2(window.localStorage.getItem('password')) === cred.password
  ) {
    return true
  }
  return false
}
