import React from 'react'
import { Redirect } from 'react-router-dom'
import css from './Form.module.css'
import cred from '../util/cred'

import { Form, Input, Icon, Button } from 'antd'

export default Form.create()(
  class extends React.Component {
    cred = (rule, value, callback) => {
      window.localStorage.setItem(rule.field, value)
      if (!cred()) {
        callback(true)
      } else {
        callback()
      }
    }

    handleSubmit = e => {
      e.preventDefault()
      this.props.form.validateFields((err, values) => {
        if (!err) {
          for (var val in values) {
            window.localStorage.setItem(val, values[val])
          }
          window.redirect('/success')
        }
      })
    }

    render() {
      const { getFieldDecorator } = this.props.form
      if (cred()) {
        return <Redirect to="/success" />
      }
      return (
        <div className={css.Form}>
          <Form onSubmit={this.handleSubmit} className={css.Box}>
            <Form.Item>
              {getFieldDecorator('username', {
                validateFirst: true,
                rules: [
                  { required: true, message: 'This is a required field!' },
                  {
                    validator: this.cred,
                    message: 'Incorrect username or password!'
                  }
                ]
              })(
                <Input
                  prefix={
                    <Icon type="user" style={{ color: 'rgba(0, 0, 0, .25)' }} />
                  }
                  placeholder="Username"
                  autoComplete="username"
                />
              )}
            </Form.Item>
            <Form.Item>
              {getFieldDecorator('password', {
                validateFirst: true,
                rules: [
                  { required: true, message: 'This is a required field!' },
                  {
                    validator: this.cred,
                    message: 'Incorrect username or password!'
                  }
                ]
              })(
                <Input
                  prefix={
                    <Icon type="lock" style={{ color: 'rgba(0, 0, 0, .25)' }} />
                  }
                  type="password"
                  placeholder="Password"
                  autoComplete="current-password"
                />
              )}
            </Form.Item>
            <Button type="primary" htmlType="submit" className={css.Button}>
              Log in
            </Button>
          </Form>
        </div>
      )
    }
  }
)
